import React from 'react';
import { withRouter } from 'react-router-dom';

const Link = ({ href, location, history, to }) => {
  const styles = {
    textDecoration: 'none',
    display: 'inline-block',
    color: location.pathname === href ? '#fff' : '#d4d4d4',
    margin: '0 1rem'
  };

  const handleClick = e => {
    e.preventDefault();
    history.push(href);
  };

  return (
    <a href={href} onClick={handleClick} style={styles}>
      {to}
    </a>
  );
};

export default withRouter(Link);
